@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&family=Roboto:wght@500&display=swap');

.main{
    padding:10px;
    height:100vh;
}
.layout{
    display: flex;
}
body{
    background: #ad5389;  
    background: -webkit-linear-gradient(to right, #3c1053, #ad5389);  
    background: linear-gradient(to right, #3c1053, #ad5389); 
}
.sidebar{
    /* height: 636px; */
    /* height:40rem; */
    min-height: 100%;
   
    width: 300px;
    border-radius: 5px;
    background-color:rgba(70, 51, 87, 0.658) ;
    box-shadow: 0px 0px 10px gray;
    margin-right: 20px;
    color: white
}
.content{
    width: 100%;
    height: 100%;
}
.header{
    height: 10vh;
    margin-bottom: 20px; 
    border-radius: 4px;
    box-shadow: 0px 0px 10px gray;
    background: #D3CCE3;  
    background: -webkit-linear-gradient(to right, #E9E4F0, #D3CCE3); 
    background: linear-gradient(to right, #E9E4F0, #D3CCE3); 

}
.body{
    height: 120vh;
    /* height:35rem; */
  
    margin-bottom: 20px;
    border-radius: 4px;
    box-shadow: 0px 0px 10px rgb(52, 10, 10);
    background: #D3CCE3;  
    background: -webkit-linear-gradient(to right, #E9E4F0, #D3CCE3); 
    background: linear-gradient(to right, #E9E4F0, #D3CCE3); 
    font-family: 'Playfair Display', serif; 

}
.logo h6{
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 0px;
}
.menu{
    margin-top: 50px;
}
.menu-item{
    margin-top:30px;
}
.menu-item a{
    color:white;
    text-decoration:none;
    font-size:1.2rem;
}
.menu-item i{
    font-size: 1.2rem;
    margin:0 15px;
}
.active{
    background-color: white;
    padding:10px;
}
.active a,.active i{
    color: blueviolet;
}
.header-content{
    display:flex;
    align-items: center;
    height: 50px;
    justify-content:flex-end;
    margin:0 15px;
}
.header-content i{
    margin-right: 20px;
    font-size: 1.2rem;
}
.header-content a{
    text-decoration: none;
    color: blue;
    margin: 0 10px;
    font-size: 1.2rem;
    text-transform:capitalize;
}